import uniRate from '@/components/uni-rate/uni-rate.vue'
export default {
	components: {
		uniRate
	},
	data() {
		return {
			customerId:0,
			customerName:"请选择客户",
			paimaiId:0,
			houseSource:"",
			houseName:"请选择房源",
			evaluateScore:0,
			content:"",
			hasclickback:false
		}
	},
	onShow() {
		console.log(this.userId)
	},
	methods: {
		getRate(e){
			const that = this;
			that.evaluateScore=e.value
		},
		bindTextAreaBlur(e){
			this.content=e.detail.value;
		},
		sub(){
			const that = this;
			setTimeout(()=>{
				if(that.customerId==0||that.paimaiId==0||that.evaluateScore==0||that.textArea==""){
					uni.showToast({
						title:"请输入必填内容",
						icon:"none"
					})
				}
				else{
					
					uni.showModal({
						content: "日志提交后将不可再次修改，确认提交日志？",
						confirmText: "确认提交",
						cancelText: "取消",
						success: function (res) {
							if (res.confirm) {
								uni.showLoading({
									title:"提交中..."
								})
								let param = {
									paimaiId: that.paimaiId,
									houseSource: that.houseSource,
									customerId:that.customerId,
									content:that.content,
									evaluateScore:that.evaluateScore
								}
								that.$api.dailyAdd(param).then(res => {
									if (res.success) {
										uni.showToast({
											title:"提交成功"
										})
										setTimeout(()=>{
											that.$Router.back(1);
										},1000)	
										var pages = getCurrentPages(); //当前页
										var beforePage = pages[pages.length - 2].route; //上个页面
										if(beforePage=="pages/my/manageNavs/journal/list"){
											that.prevPageReload();
										}			
									} else {
										uni.showToast({
											title: res.message,
											icon: "none"
										})
									}
								})
								// uni.showToast({
								// 	title:"提交成功"
								// })
							} else if (res.cancel) {
								
							}	
						}
					});	
					
				}
			},100)
		},
		prevPageReload(){
			var pages = getCurrentPages(); //当前页
			var beforePage = pages[pages.length - 2]; //上个页面




			beforePage.onLoad()

		},
		searchCustomer(){
			const that = this;
			that.$Router.push({
				path:"/pages/my/manageNavs/searchCustomer"
			})
		},
		searchHouse(){
			const that = this;
			that.$Router.push({
				path:"/pages/my/manageNavs/searchHouse"
			})
		},
		back(){
			const that = this;
			if(that.paimaiId!=0||that.customerId!=0||that.evaluateScore!=0||that.content!=""){	
				uni.showModal({
						content: "退出后将不保留已编辑的内容，确认退出日志编辑？",
						confirmText: "继续编辑",
						cancelText: "退出",
					success: function (res) {
						if (res.confirm) {
				
						} else if (res.cancel) {
							that.$Router.back(1);
						}	
					}
				});		
			}
			else{
				that.$Router.back(1);
			}		
		}
	},
	
}
